<template>
  <div>
    <navPatient/>
    <div class="container bg-light">
      <h1 class="text-center text-dark p-5">
        <b>Datos del Paciente</b>
      </h1>
      <h3 class="text-center text-dark">Realice los cambios que desea modificar</h3>
      <div style="width:400px">
        <b-alert show variant="success" style="width:400px" v-if="isCorrect">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
        <b-alert show variant="danger" style="width:400px" v-else-if="isCorrect!==null">
          <h4 class="alert-heading">{{avise}}</h4>
          <p>{{message}}</p>
        </b-alert>
      </div>
      <form @submit="save">
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="nombre">Nombres</label>
            <input
              class="form-control"
              placeholder="Ingrese nombres"
              v-model="patients.nombre"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="nombre">Apellidos</label>
            <input
              class="form-control"
              placeholder="Ingrese apellidos"
              v-model="patients.apellido"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="tipoDocumento">Tipo de Documento</label>
            <select
              class="custom-select"
              id="tipoDocumento"
              v-model.number="tipoDocumento.id"
              required
            >
              <option disabled value>--Seleccione--</option>
              <option value="1">Pasaporte</option>
              <option value="2">Cedula</option>
            </select>
          </div>
          <div class="col-md-6">
            <label for="numDocumento">Numero de Documento</label>
            <input
              class="form-control"
              placeholder="Numero de documento"
              maxlength="10"
              v-model="patients.numDocumento"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <div class="col-md-6">
            <label for="domicilio">Domicilio</label>
            <input
              class="form-control"
              placeholder="Domicilio"
              v-model="patients.domicilio"
              type="text"
              required
            >
          </div>
          <div class="col-md-6">
            <label for="obraSocial">Obra Social</label>
            <input
              class="form-control"
              placeholder="Obra Social"
              v-model="patients.obraSocial"
              type="text"
              required
            >
          </div>
        </div>
        <div class="form-row p-5">
          <button class="btn btn-primary">Enviar</button>
        </div>
      </form>
    </div>
    <Footer/>
  </div>
</template>
<script>
import { URL, bootstrap, bootbox } from "@/variables.js";
import axios from "axios";
import navPatient from "@/components/patient/navPatient.vue";
import Footer from "@/components/Footer.vue";
export default {
  name: "editarPaciente",
  data() {
    return {
      id: this.$route.params.id,
      patients: [],
      message: null,
      isCorrect: null,
      avise: null,
      tipoDocumento: {
        id: null
      }
    };
  },
  methods: {
    async getPatientById() {
      axios
        .get(`${URL}/api/paciente/${this.id}`)
        .then(response => {
          this.tipoDocumento = response.data.tipoDocumento;
          this.patients = response.data;
        })
        .catch(error => console.log(error));
    },
    save(e) {
      e.preventDefault();
      this.avise = "";
      this.message = "";
      axios
        .put(`${URL}/api/paciente/${this.id}`, {
          tipoDocumento: { id: this.tipoDocumento.id },
          medico:
            this.patients.medico === null
              ? this.patients.medico
              : { id: this.patients.medico.id },
          apellido: this.patients.apellido,
          nombre: this.patients.nombre,
          numDocumento: this.patients.numDocumento,
          domicilio: this.patients.domicilio,
          obraSocial: this.patients.obraSocial
        })
        .then(response => {
          bootbox.alert({
            title: "Datos Actualizados",
            message: response.data.mensaje,
            locale: "es",
            backdrop: true,
            callback: this.$router.push("/pacientes")
          });
        })
        .catch(error => {
          this.avise = "Error";
          this.isCorrect = false;
          this.message = "Detalle: " + error.response.data.mensaje;
          bootbox.alert({
            title: "Error",
            message: "No se pudo editar",
            locale: "es",
            backdrop: true
          });
          console.log("Error " + error.response.status + " Conflict");
        });
    }
  },
  components: {
    navPatient,
    Footer
  },
  mounted() {
    this.getPatientById();
  }
};
</script>

